export enum FileTypeApi {
  PNG = 'PNG',
  JPEG = 'JPEG',
  IMG = 'IMG',
  JPG = 'JPG',
  GIF = 'GIF',
  TXT = 'TXT',
  PDF = 'PDF',
  DOC = 'DOC',
  DOCX = 'DOCX',
  XLS = 'XLS',
  XLSX = 'XLSX',
  PPT = 'PPT',
  PPTX = 'PPTX',
  AAC = 'AAC',
  MP3 = 'MP3',
  AVI = 'AVI',
  M4A = 'M4A',
  MP4 = 'MP4',
  MOV = 'MOV',
  PCM = 'PCM',
  PROTO = 'PROTO',
  IPS = 'IPS',
}

