export interface PickerAssetExtension {
  name: string;
  fileType: FileTypeApi;
  mimeType: keyof typeof fileTypesApi;
  size: number;
  fileSize: number;
}

export const fileTypesApi = {
  'image/png': FileTypeApi.PNG,
  'image/jpeg': FileTypeApi.JPEG,
  'image/jpg': FileTypeApi.JPG,
  'image/gif': FileTypeApi.GIF,
  'text/plain': FileTypeApi.TXT,
  // NOT SUPPORTED BY WEB
  'video/x-msvideo': FileTypeApi.AVI,
  'audio/mpeg': FileTypeApi.MP3,
  'audio/aac': FileTypeApi.AAC,
  'audio/mp4': FileTypeApi.M4A,
  'video/mp4': FileTypeApi.MP4,
  'video/quicktime': FileTypeApi.MOV,
  // NOT SUPPORTED BY WEB
  'application/pdf': FileTypeApi.PDF,
  'application/vnd.ms-excel': FileTypeApi.XLS,
  'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet':
    FileTypeApi.XLSX,
  'application/msword': FileTypeApi.DOC,
  'application/vnd.openxmlformats-officedocument.wordprocessingml.document':
    FileTypeApi.DOCX,
  'application/vnd.ms-powerpoint': FileTypeApi.PPT,
  'application/vnd.openxmlformats-officedocument.presentationml.presentation':
    FileTypeApi.PPTX,
};

export interface FileUploadRequestType {
  fileSize: string;
  fileType: FileTypeApi;
}

export interface FileDownloadRequestType {
  relativePath: string;
}

export type FilePreviewScreenParams =
  | FilePreviewScreenUploadParams
  | FilePreviewScreenViewParams;

export interface FilePreviewScreenViewParams {
  chatId: string;
  message: ChatMessage;
  dontRenderHeader?: true;
  style?: ViewStyle;
}

export type FilePreviewScreenUploadParams =
  | FilePreviewScreenUploadLocalParams
  | FilePreviewScreenUploadExternalParams;

export interface FilePreviewScreenUploadLocalParams {
  isEditable: true;
  chatId: string;
  asset: DocumentPickerAsset;
  fileType: FileTypeApi;
}

export interface FilePreviewScreenUploadExternalParams {
  chatId: string;
  isEditable: true;
  fileUrl: string;
  fileType: FileTypeApi;
}
